import React from 'react';
import classNames from 'classnames';
import FullCalendar from 'fullcalendar-reactwrapper';
import moment from 'moment';

class ExampleComponent extends React.Component {
  render() {
    const { events, date, view, onViewChange } = this.props;

    return (
      <div className="main-calendar-container">
        <div className="header">
          <div className="current-date">{moment(date).format('MMMM D, YYYY')}</div>
          <div className="btn-group">
            <button
              className={classNames('btn mac-btn', { active: view === 'agendaDay' })}
              onClick={onViewChange('agendaDay')}
            >
              day
            </button>
            <button
              className={classNames('btn mac-btn', { active: view === 'agendaWeek' })}
              onClick={onViewChange('agendaWeek')}
            >
              week
            </button>
            <button
              className={classNames('btn mac-btn', { active: view === 'month' })}
              onClick={onViewChange('month')}
            >
              month
            </button>
          </div>
        </div>
        <FullCalendar
          id="calendar-view"
          header={{
            left: '',
            center: 'title',
            right: 'agendaDay,agendaWeek,month'
          }}
          defaultDate={date}
          // navLinks // can click day/week names to navigate views
          editable
          eventLimit // allow "more" link when too many events
          events={events}
          // dayClick={(moment, jqEvent, members) => console.log(moment, jqEvent, members)}
          // noEventsMessage="НИ-ЧЕ-ГО"
          // locale="ru"
          // theme="sketchy"
          defaultView={view}
        />
      </div>
    );
  }
}

export default ExampleComponent;
