import React from 'react';
import SvgIcon from './SvgIcon';

import ava0 from '../images/ava0.jpg';
import ava1 from '../images/ava1.jpg';
import ava2 from '../images/ava2.jpg';
import ava3 from '../images/ava3.png';

const avatars = [
  ava0,
  ava1,
  ava2,
  ava3,
];

const currentAvatar = avatars[Math.floor(Math.random() * avatars.length)];

const Sidebar = () => (
  <div className="sidebar">
    <div className="header">
      <img src={currentAvatar} alt="Аватар пользователя" className="avatar" />
      <div className="fullname">Андрей Викторович</div>
      <div className="badge">premium</div>
    </div>
    <div className="main">
      <div className="title">Menu</div>
      <ul className="menu">
        <li className="menu-item active">
          <SvgIcon id="calendar" className="menu-icon" />
          <span className="menu-title">Calendar</span>
        </li>
        <li className="menu-item">
          <SvgIcon id="document" className="menu-icon" />
          <span className="menu-title">Notes</span>
        </li>
        <li className="menu-item">
          <SvgIcon id="bell" className="menu-icon" />
          <span className="menu-title">Alerts</span>
        </li>
        <li className="menu-item">
          <SvgIcon id="homework" className="menu-icon" />
          <span className="menu-title">Tasks</span>
        </li>
        <li className="menu-item">
          <SvgIcon id="target" className="menu-icon" />
          <span className="menu-title">Purposes</span>
        </li>
      </ul>
    </div>
    <div className="footer">
      <ul className="menu">
        <li className="menu-item">
          <SvgIcon id="setup" className="menu-icon" />
          <span className="menu-title">Settings</span>
        </li>
      </ul>
    </div>
  </div>
);

export default Sidebar;
