import React, { Component } from 'react';

import MainCalendar from './Calendar';
import RightAside from './RightAside';
import Sidebar from './Sidebar';
import moment from 'moment';

const events = [
  {
    title: 'All Day Event',
    start: '2018-12-01'
  },
  {
    title: 'Long Event',
    start: '2018-12-07',
    end: '2018-12-10'
  },
  {
    id: 999,
    title: 'Repeating Event',
    start: '2018-12-09T16:00:00'
  },
  {
    id: 999,
    title: 'Repeating Event',
    start: '2018-12-16T16:00:00'
  },
  {
    title: 'Conference',
    start: '2018-12-11',
    end: '2018-12-13'
  },
  {
    title: 'Meeting',
    start: '2018-12-12T10:30:00',
    end: '2018-12-12T12:30:00'
  },
  {
    title: 'Birthday Party',
    start: '2018-12-13T07:00:00'
  },
  {
    title: 'Click for Google',
    url: 'http://google.com/',
    start: '2018-12-28'
  }
];

class App extends Component {
  state = {
    date: moment().format(),
    view: 'month'
  };

  handleViewChange = view => () => this.setState({ view });

  handleDayChange = date => this.setState({ date });

  handleMonthChange = date => this.setState(prevState => {
    const month = moment(date).month();
    const prevMonth = moment(prevState.date).month();
    if (prevMonth !== month) {
      return { date };
    }

    return { date: moment(prevState.date).month(month).format() };
  });

  render() {
    const { date, view } = this.state;

    return (
      <div className="App">
        <Sidebar />
        <MainCalendar events={events} date={date} view={view} onViewChange={this.handleViewChange} />
        <RightAside
          events={events}
          date={date}
          onDayChange={this.handleDayChange}
          onMonthChange={this.handleMonthChange}
        />
      </div>
    );
  }
}

export default App;
